p {
  display: block;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}
ul {
  display: block;
  list-style-type: disc;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px;
}
.profile {
  border: 1px solid gray;
  max-width: 320px;
  width: 100%;
  margin: 0 auto;
  padding: 20px 3px 3px 3px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 0 10px 5px rgba(93, 228, 233, 0.3);
}
.description {
  display: inline-block;
  text-align: center;
  line-height: 1.667;
  color: black;
  justify-content: center;
  margin: 0 auto;
  width: 100%;
}
.name {
  font-size: 24px;
  font-weight: bold;
  width: 100%;
  margin: 0 auto;
  text-shadow: 1px 1px 2px gray;
}
.tag,
.location {
  color: gray;
}
.avatar {
  width: 60%;
  height: 190px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  border-radius: 50%;
  box-shadow: 0 0 10px 3px rgba(93, 228, 233, 0.3);
}

.stats {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  border-radius: 8px;
  margin-top: 20px;
}
.stats li {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  font-size: small;
  border: 1px solid gray;

  width: 100%;
  margin: 0 auto;
  padding: 10px 0;
  border-radius: 8px;
  box-shadow: 0 0 10px 5px rgb(93 228 233 / 20%);
}
.stats li:not(:last-child) {
  margin-right: 3px;
}
.label {
  color: gray;
}
.quantity {
  font-weight: bold;
}
